interface Rules {
  r0: string;
  r1: Array<string>;
}

export class Rhythm {
  private rules: Rules;
  private startSequence = '110111101110'


  constructor() {
    this.rules = {
      r0: "0",
      r1: ["01", "10", "11", "12"],
    };
  }

  returnStartSequence(){
    return this.startSequence

  }

  //Expands the seed (ex. "1010") for #iterations times according to this.rules
  expand(seed: string, subdivisions:number, iterations: number): string {
    let supp = [];
    let out: string = seed;
    for (let i = 0; i < iterations; i++) {
      for (let j = 0; j < out.length; j++) {
        let position = j % subdivisions

        switch (position) {
          case 0:
            supp.push("0")
            break;
          case 1:
            supp.push(
              this.rules.r1[Math.floor(Math.random() * this.rules.r1.length)]
            );
            break;
          case 2:
            supp.push("2")
			break;
		  case 3:
			break;

          default:
            break;
        }

           switch (out[j]) {
                    case '0':
                        supp.push('0')
                        break
                    case '1':
                        supp.push(this.rules.r1[Math.floor(Math.random() * this.rules.r1.length)])
                        break
                    case '2':
                        supp.push('2')
                    default:
                        break
                }
            
      }
      out = supp.toString().replace(/,/g, "");
      supp = [];
    }
    while (out.length % 4 != 0) {
      out += "0";
    }

    return out;
  }
}
