import { Vector3 } from "three";
import { AudioSource } from "./audioSource";
import * as Tone from "tone";
import { Sampler } from "tone";
import grainG1 from '../../assets/samples/grainG.mp3'
import grainG2 from '../../assets/samples/grainG2.mp3'

export class NoiseGenerator extends AudioSource {
  private sampler: Tone.Sampler;
  private grainplayer: Tone.GrainPlayer;
  private currentIndex: number = -1 // keeps track of the third movement

  private sampleList: string[] = ['G1', 'B0', 'D#1']

  constructor(position: number,  duration: string, rhythmicStructure: string) {
    super(position, rhythmicStructure)
    console.log(rhythmicStructure)
    this.sampler = new Sampler({
      urls: {
        G1: grainG1,
        G2: grainG2,
      }   
    })

    this.grainplayer = new Tone.GrainPlayer()

    this.gain = new Tone.Gain(0.2).toDestination()
    this.delay = new Tone.PingPongDelay(0.1, 0.9)
    this.reverb = new Tone.Reverb({decay: 1.0, wet:0.6})

    this.grainplayer.chain(this.delay, this.reverb, this.gain)
    this.sampler.chain(this.delay, this.reverb, this.gain)

    this.noteDuration = duration

    this.initializeLoop()
  }

  initializeLoop() {
    this.loop = new Tone.Loop((time) => {
     
      
     if (this.rhythmicStructure[this.count] == "2") { //trigger granular synth
          this.sampler.triggerAttackRelease(this.sampleList[this.currentIndex], '4n')
          console.log(this.sampleList[this.currentIndex])
      }
      this.count == this.rhythmicStructure.length - 1 ? (this.count = 0) : this.count++;
    }, "1m");   //Loop duration
  }
  
  play(){
    if(!this.isPlaying){
      this.loop.start()
      this.isPlaying = true
    }
  }

  stop(){
    if(this.isPlaying){
      this.loop.stop()
      this.grainplayer.stop()
      this.isPlaying = false
    }
  }

  openFilter(amt: number){/*do nothing*/}
  changeMode(mode: string) {
    this.currentIndex = (this.currentIndex +1) % 3

  }
  getMode(): string {return ""}
}