import * as THREE from "three";
import { Box3 } from "three";

const font = require("../../assets/fonts/Anders_Regular.json")
const glslify = require("glslify")

const SUBTEXT_Y = -100

export class Text {

  private text: THREE.TextBufferGeometry;
  private subtext: THREE.TextBufferGeometry;
  private textPoints: THREE.Vector3[];
  private particleText: THREE.Points;
  private subText: THREE.Mesh;
  private topText: THREE.Mesh;
  private textMaterial: THREE.RawShaderMaterial;
  private font: THREE.Font;
  private textUniforms: any;
  private textBox: Box3
  private textBoxMesh: THREE.Mesh

  constructor() {
    //text
    this.font = new THREE.Font(font);
    this.textPoints = [];
    this.textBox = new THREE.Box3()
  }

  createText() {
    this.text = new THREE.TextBufferGeometry("SYNTHETIC SPACES", {
      font: this.font,
      size: 100,
      height: 8,
      curveSegments: 10,
      bevelEnabled: true,
      bevelThickness: 3,
      bevelSize: 2,
      bevelOffset: 0,
      bevelSegments: 5,
    });
    this.text.center();
    this.text.computeBoundingBox();
    this.topText = new THREE.Mesh(this.text, new THREE.MeshBasicMaterial());

    this.subtext = new THREE.TextBufferGeometry("Press space to start", {
      font: this.font,
      size: 30,
      height: 5,
      curveSegments: 10,
      bevelEnabled: false,
    });
    this.subtext.computeBoundingBox();
    this.subtext.center();
    this.subtext.translate(0, SUBTEXT_Y, 0);
    this.subText = new THREE.Mesh(this.subtext, new THREE.MeshBasicMaterial());

    let shape = this.font.generateShapes("SYNTHETIC SPACES", 100);

    let g = new THREE.ShapeBufferGeometry(shape);
    g.computeBoundingSphere();

    g.center();
    let holeShapes = [];

    for (let q = 0; q < shape.length; q++) {
      // thanks to to mr. RICARDO SANPRIETO for the tip on how to do this
      let s = shape[q];
      if (s.holes && s.holes.length > 0) {
        for (let j = 0; j < s.holes.length; j++) {
          let hole = s.holes[j];
          holeShapes.push(hole);
        }
      }
    }
    shape.push.apply(shape, holeShapes);

    for (let x = 0; x < shape.length; x++) {
      let s = shape[x];
      let segment = s.getSpacedPoints(80);
      segment.forEach((element, z) => {
        const a = new THREE.Vector3(element.x, element.y, Math.random());
        this.textPoints.push(a);
      });
    }

    let geom = new THREE.BufferGeometry().setFromPoints(this.textPoints);
    geom.center();

    this.textUniforms = {
      uPos: { type: "v3", value: new THREE.Vector3(1000, 100000, 100000) },
      uTimeT: { type: "f", value: 0 },
    };

    this.textMaterial = new THREE.ShaderMaterial({
      vertexShader: glslify(require("../../shaders/text.vert")),
      fragmentShader: glslify(require("../../shaders/text.frag")),
      uniforms: this.textUniforms,
    });

    this.particleText = new THREE.Points(geom, this.textMaterial);

    // Creating the bounding box for raycasting
    this.textBox.copy(this.text.boundingBox).applyMatrix4(this.topText.matrixWorld)
    let dimensions = new THREE.Vector3().subVectors(this.textBox.max, this.textBox.min)
    let boxGeo = new THREE.BoxBufferGeometry(dimensions.x, dimensions.y, dimensions.z)
    this.textBoxMesh = new THREE.Mesh(boxGeo, new THREE.MeshBasicMaterial( { color: 0xffcc55 } ))
    this.textBoxMesh.visible = false
  }

  update(time: number) {
    if (this.textUniforms) {
      this.textUniforms.uTimeT.value = time
    }
  }

  animate(x: number, y: number, z: number, t: number) {
    if (this.textUniforms) {
      this.textUniforms.uPos.value = new THREE.Vector3(x, y, z);
      this.textUniforms.uTimeT.value = t
    }
  }

  getText() {
    return { particleText: this.particleText, subText: this.subText, topText: this.topText}
  }

  getBoundingBoxMesh() {
    return this.textBoxMesh
  }

}