import { Vector3 } from "three";
import { AudioSource } from "./audioSource";
import * as Tone from "tone";
import { Melody } from "../grammars/Melody";

const SUBDIVISIONS = [2,3,4,6]

export class SparseNotes extends AudioSource {
  private synth: Tone.Synth
  private thirds: number   //keep track of thirds movement
  private newRhythm:string[] // recombines the rhythm to give some variation

  constructor(
    position: number,
    root: string,
    attack: number,
    decay: number,
    sustain: number,
    release: number,
    duration: string,
    rhythmicStructure: string
  ) {
    super(position, rhythmicStructure)

    this.root = root;
    this.envelope = {
      attack: attack,
      decay: decay,
      sustain: sustain,
      release: release,
    };
    this.noteDuration = duration

    this.melody = new Melody(0)

    this.synth = new Tone.Synth({
      oscillator: {
        type: 'triangle4'
      },
      envelope: {
        attack: this.envelope.attack,
        decay: this.envelope.decay,
        release: this.envelope.release,
        sustain: this.envelope.sustain,
      },
      volume:-18
    });
    this.filter = new Tone.Filter(1000, "lowpass")
    this.delay = new Tone.PingPongDelay(0.1, 0.9)
    this.reverb = new Tone.Reverb({decay: 8.0, wet:0.8})
    this.gain = new Tone.Gain(0.4).toDestination()
    
    this.synth.chain(this.filter, this.delay, this.reverb, this.panner, this.gain)  //synth -> filter -> delay -> reverb -> gain

    this.initializeLoop()

    this.thirds = 0
    this.newRhythm = this.rhythmicStructure.split("")
  }

  initializeLoop() {
    this.loop = new Tone.Loop(() => {
      if (this.newRhythm[this.count] == "1") {
        this.triggerNote()
      }
      this.count == this.newRhythm.length - 1 ? this.moveRhythm() : this.count++

    }, '4n')    //Loop duration
  }

  play() {
    if(!this.isPlaying){
      this.loop.start()
      this.isPlaying = true
    }
  }

  stop() {
    if(this.isPlaying){
      this.loop.stop()
      this.isPlaying = false
    }
  }
  
  openFilter(amt: number){
    this.filter.frequency.value =  1200 + amt * 500.0;
  }

   changeMode(mode: string) {

    super.changeMode(mode)
    
    this.thirds = (this.thirds+1) % 3  
    this.count = 0
  }

  triggerNote(){
    let next = this.melody.nextNote()
    this.synth.triggerAttackRelease(Tone.Frequency(this.root).transpose(next + this.thirds*4).toFrequency(), this.noteDuration)

  }

  moveRhythm(){

    Math.random() > 0.4 ? this.permute(4) : null
    Math.random() > 0.8 ? this.reverse : null

    this.count = 0

  }

  reverse(){ // reverse
    console.log('reverse!')
    this.newRhythm = this.newRhythm.reverse()
  }

  permute(subdivisions:number){
    console.log('permute!')
    let chunks = []
    for (let i = 0; i < this.newRhythm.length; i+=subdivisions){
      let tmp = this.newRhythm.slice(i, i+subdivisions)
      chunks.push(tmp)
    }
    let narray=[]
    for (let c in chunks){
      let rnd = Math.floor(Math.random()*(chunks.length))
      narray.push(chunks[rnd])
    }
    this.newRhythm = [].concat.apply([],narray)

  }

}
