
const API_KEY_WEATHER = "10082cbf006e2c41c9d1a90cfa631624"
const API_KEY_TRAFFIC = "rZmC9FluQ_Xxo4-HlFUVar9SV0kzR0f6eBX1oLGUhF4"

const cities = ['Tokyo', 'Paris', 'Marrakech' , 'Berlin', 'Havana', 'Barcelona', 'Milan', 'Mexico City']

export class Data {
    private latitude: Array<number>     // Latitudes of locations
    private longitude: Array<number>    // Longitudes of locations
    private radius: number              // Radius of the considered area 
    private xhr: XMLHttpRequest
    private xhrTraffic: XMLHttpRequest

    private weatherData:string[] = []


    constructor() {
        this.latitude = [45.46424081318447]
        this.longitude = [9.189939963463065]
        this.radius = 1000
    }

    setCity(city: string) {
        this.xhr.open('GET',
            "https://api.openweathermap.org/data/2.5/weather?q=" + city + "&appid=" + API_KEY_WEATHER)
    }

    setCityTraffic(lat: number, long: number, radius: number) {
        this.xhrTraffic.open('GET',
            "https://traffic.ls.hereapi.com/traffic/6.2/flow.json?apiKey=" + API_KEY_TRAFFIC + "&prox=" + lat + "," + long + "," + radius)
    }

    getCityName(index: number) {
        return cities[index % cities.length]
    }

    async loadWeather(city:string | void):Promise<string> {

        return new Promise((resolve, reject) => {
            this.xhr = new XMLHttpRequest()

            if (city) this.setCity(city)
            this.xhr.onload = () => {
                var obj = JSON.parse(this.xhr.response).weather[0].main
                resolve(obj)

            }
            this.xhr.onerror = () => reject(this.xhr.statusText)
            this.xhr.send()
        });

    }

    async getTraffic(): Promise<number> {
        return new Promise((resolve, reject) => 
        {
            this.xhrTraffic = new XMLHttpRequest()
            this.setCityTraffic(this.latitude[0], this.longitude[0], this.radius)

            this.xhrTraffic.onload = () => 
            {
                let streets = JSON.parse(this.xhrTraffic.response).RWS[0].RW
                let jam_factor = this.calculateAverageTraffic(streets)
                resolve(jam_factor)
            }
            
            this.xhrTraffic.onerror = () => reject(this.xhrTraffic.statusText)
            this.xhrTraffic.send()
        })
    }

    async initWeather(){
        for (var city of cities){
            await this.loadWeather(city).then( (response) => {
                this.weatherData.push(response)
            })
        }
    }

    weatherOf(index:number):string{
       return this.weatherData[index % cities.length]
    }

    calculateAverageTraffic(streets: Array<any>): number {
        let flow_items: Array<any> = []
        let n_elem: number = 0
        let average: number = 0

        streets.forEach((street) => 
        {
            flow_items = street.FIS[0].FI
            flow_items.forEach((item) => 
            {
                console.log(item)
                average += item.CF[0].JF
                n_elem++
            })
        })
        
        average /= n_elem
        return average
    }
}
